<template>
  <v-container>
    <add-slider v-on:getVal="getVal" />
    <v-data-table
      :calculate-widths="true"
      :headers="headers"
      :items="
        items.map((item) => {
          return {
            title: item.title,
            description: item.description,
            url: item.url,
            id: item.id,
          };
        })
      "
      hide-default-footer
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Slider Items </v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-row>
          <v-col cols="2">
            <edit-slider v-on:getVal="getVal" :item="item" />
          </v-col>
          <v-col cols="2">
            <delete-slider v-on:getVal="getVal" :item="item" />
          </v-col>
        </v-row>
      </template>
      <template v-slot:no-data>
        <v-progress-circular
          v-if="isDone"
          class="m-a5"
          :width="5"
          color="primary"
          indeterminate
        ></v-progress-circular>
      </template>
      <template v-slot:item.url="{ item }">
        <v-row justify="center">
          <v-container min-hight="100">
            <v-img
              id="image"
              :src="minUrlImage + item.url"
              :lazy-src="minUrlImage + item.url"
              max-width="150"
              max-height="150"
            >
              <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center">
                  <v-progress-circular
                    indeterminate
                    color="grey lighten-5"
                  ></v-progress-circular>
                </v-row>
              </template>
            </v-img>
          </v-container>
        </v-row>
      </template>
      <template v-slot:item.title="{ item }">
        <v-card-text>
          {{ item.title[lang] }}
        </v-card-text>
      </template>
      <template v-slot:item.description="{ item }">
        <v-card-text>
          {{ item.description[lang] }}
        </v-card-text>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import { sliderController } from "../../components/slider/slider";
export default {
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  data() {
    return {
      minUrlImage: this.$store.state.imageUlr + "slider/",

      isDone: false,
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: " title",
          align: "start",
          sortable: false,
          value: "title",
        },
        { text: "description", value: "description" },
        { text: "image", value: "url" },
        { text: "Actions", value: "actions", sortable: false },
      ],
      items: [],
    };
  },

  created() {
    this.initialize();
  },

  methods: {
    getVal() {
      this.items = [];
      this.initialize();
    },
    initialize: async function () {
      this.isDone = true;
      var items = await sliderController.getItems();
      this.isDone = false;

      if (items) {
        this.items = items;
      }
    },
  },
  components: {
    "edit-slider": () => import("../../components/slider/edit.vue"),
    "delete-slider": () => import("../../components/slider/delete.vue"),
    "add-slider": () => import("../../components/slider/add.vue"),
  },
};
</script>
<style>
#image {
  width: 100%;
  height: 100px;
  margin: 10px;
}
</style>